import { fetchIcon } from './registerIcon';

export interface IconRawFormat {
    d: string[];
    viewBox: string;
    rect: { x: number; y: number; width: number; height: number };
    translateToCenter: { x: number; y: number };
}

let _domParser;
const _cache = {};
export const getIconSvgElement = (icon): SVGElement => {
    const stashedIcon = fetchIcon(icon);
    if (!_domParser) {
        _domParser = new DOMParser();
    }
    return _domParser.parseFromString(stashedIcon, 'image/svg+xml').firstElementChild as SVGElement;
};

export const getIconRawFormat = (icon): IconRawFormat => {
    const cached = _cache[icon];
    if (cached) {
        return cached;
    }

    const svg = getIconSvgElement(icon);
    const paths = [...getIconSvgElement(icon).querySelectorAll('path')];
    const viewBox = svg.getAttribute('viewBox') ?? '';
    const [x, y, width, height] = viewBox.split(' ').map(parseFloat);
    _cache[icon] = {
        viewBox,
        rect: { x, y, width, height },
        translateToCenter: { x: x + width / 2, y: y + height / 2 },
        d: paths.map((p) => p.getAttribute('d')!),
    };
    return _cache[icon];
};

/** @deprecated - kinda unuseful since it returns partial icon data (one path and no viewbox) **/
export const getIconDataValue = (icon): string => {
    return getIconRawFormat(icon).d?.[0] ?? '';
};
