import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    property,
    state,
    unsafeHTML,
} from '@umetrics/sartorius-ui-base';

import { version } from './metadata';
import { IconProperties } from './iconProperties';
import { iconStyles } from './iconStyle';
import { IconSize } from './iconSize';
import { fetchIcon } from './registerIcon';

@defineElement({ tagName: 'icon', styles: iconStyles, version })
export class Icon extends BaseElement implements IconProperties {
    // Static members
    static iconResolver?: (icon: string) => Promise<any>;

    // Public members
    @property({ type: IconSize })
    size;

    @property({ type: Boolean })
    public get spin() {
        return this._spin;
    }

    public set spin(val: boolean) {
        const prev = this._spin;
        if (prev !== val) {
            this._spin = val;
            this.requestUpdate('spin', prev);
        }
    }

    @property({ type: Boolean, reflect: true })
    public get pulse() {
        return this._pulse;
    }

    public set pulse(val: boolean) {
        const prev = this._pulse;
        if (prev !== val) {
            this._pulse = val;
            this.requestUpdate('pulse', prev);
        }
    }

    @property({ type: Boolean, reflect: true, attribute: 'flip-vertical' })
    public get flipVertical(): boolean {
        return this._flipVertical;
    }

    public set flipVertical(val: boolean) {
        const prev = this._flipVertical;
        if (prev !== val) {
            this._flipVertical = val;
            this.requestUpdate('flipVertical', prev);
        }
    }

    @property({ type: Boolean, reflect: true, attribute: 'flip-horizontal' })
    public get flipHorizontal(): boolean {
        return this._flipHorizontal;
    }

    public set flipHorizontal(val: boolean) {
        const prev = this._flipHorizontal;
        if (prev !== val) {
            this._flipHorizontal = val;
            this.requestUpdate('flipHorizontal', prev);
        }
    }

    @property({ type: String, reflect: true })
    public icon;

    @property({ type: Boolean, reflect: true, attribute: 'crisp-edges' })
    public crispEdges = false;

    // Protected members
    // Private members
    private _spin = false;
    private _pulse = false;
    private _flipVertical = false;
    private _flipHorizontal = false;

    @state()
    private _svg;

    public static setIconResolver = (f: (icon) => Promise<any>) => {
        Icon.iconResolver = f;
    };

    protected shouldUpdate(changedProperties) {
        if (changedProperties.has('icon') && this.icon) {
            this._ensureIconRegistered()
                .then((data) => {
                    this._svg = data;
                })
                .catch(() => {
                    this._svg = `<svg viewBox='0 0 24 24' style='color:red;'>
                        <path d='m8.31 14.604 2.616-2.616L8.31 9.372 9.372 8.31l2.616 2.616 2.616-2.616 1.062 1.062-2.616 2.616 2.616 2.616-1.062 1.062-2.616-2.616-2.616 2.616-1.062-1.062Z'></path>
                        </svg>`;
                });
        }
        return super.shouldUpdate(changedProperties);
    }

    protected render() {
        const [, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            spin: this._spin,
            pulse: this._pulse,
            [this.size]: this.size !== undefined && this.size !== '',
            'flip-vertical': this._flipVertical,
            'flip-horizontal': this._flipHorizontal,
            'crisp-edges': this.crispEdges,
        });
        const replica = this._svg ? this._svg.replace(/<svg /, `<svg class="${rootClasses}" `) : null;
        return html`${unsafeHTML(replica)}`;
    }

    private _ensureIconRegistered = () => {
        const waitForIconRegister = (resolve, reject, depth = 0) => {
            Promise.resolve(this.icon).then((_icon) => {
                const value = fetchIcon(_icon.default ?? _icon);
                if (value) {
                    resolve(value);
                } else if (depth > 5) {
                    reject(new Error('timeout for ' + _icon));
                } else {
                    if (Icon.iconResolver) {
                        Icon.iconResolver(this.icon);
                    }
                    // 0, 40, 160, 480, 1280, 3200, error
                    const tmo = 2 ** depth * (depth * 20);
                    setTimeout(() => waitForIconRegister(resolve, reject, depth + 1), tmo < 100 ? 100 : tmo);
                }
            });
        };
        return new Promise(waitForIconRegister);
    };
}
