import { cssSelector, GenerateStyles, style, css, CSSResultArray } from '@umetrics/sartorius-ui-base';

export const iconStyles: GenerateStyles = (tagName) => {
    const { root, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host {
                display: inline-block;
                line-height: 0;
            }

            ${root} {
                fill: var(--icon-color, currentcolor);
                width: 1em;
                height: 1em;
            }
            ${is('extra-tiny')} {
                width: 0.625rem;
                height: 0.625rem;
            }
            ${is('tiny')} {
                width: 0.75rem;
                height: 0.75rem;
            }
            ${is('small')} {
                width: 1rem;
                height: 1rem;
            }
            ${is('normal')} {
                width: 1.25rem;
                height: 1.25rem;
            }
            ${is('large')} {
                width: 1.5rem;
                height: 1.5rem;
            }
            ${is('extra-large')} {
                width: 3rem;
                height: 3rem;
            }

            ${is('spin')} {
                animation: mj-spin 2s infinite linear;
            }
            ${is('pulse')} {
                animation: mj-spin 1s infinite steps(8);
            }
            ${is('crisp-edges')} {
                shape-rendering: crispEdges;
            }

            @keyframes mj-spin {
                0% {
                    transform: rotate(0deg);
                }
                100% {
                    transform: rotate(360deg);
                }
            }

            ${is('flip-horizontal')} {
                transform: scale(-1, 1);
            }
            ${is('flip-vertical')} {
                transform: scale(1, -1);
            }
        `,
    ] as CSSResultArray;
};
